<template>
  <!-- <div class='container'> -->
  <div class="swiper-wrap">
    <div class="theme-content">
      <vue-seamless-scroll
        :key="key1"
        :data="imgData1"
        class="seamless-wrap"
        :class-option="classOption1"
      >
        <div class="img-box">
          <div
            class="img-content"
            v-for="(item, index) in imgData1"
            @click="goDetail(item)"
            :key="index"
          >
            <img
              :src="
                'http://120.224.10.23:18060/navsysBoot/sys/common/static/' +
                item.attachment
              "
              alt=""
            />
          </div>
        </div>
      </vue-seamless-scroll>

      <vue-seamless-scroll
        :key="key2"
        :data="imgData2"
        class="seamless-wrap"
        :class-option="classOption2"
      >
        <div class="img-box">
          <div
            class="img-content"
            v-for="(item, index) in imgData2"
            @click="goDetail(item)"
            :key="index"
          >
            <img
              :src="
                'http://120.224.10.23:18060/navsysBoot/sys/common/static/' +
                item.attachment
              "
              alt=""
            />
          </div>
        </div>
      </vue-seamless-scroll>
    </div>

    <el-dialog
      :visible.sync="dialogVisible"
      width="50%"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      custom-class="custom-img-dialog"
    >
      <i class="el-icon-circle-close" @click="closeDialog"></i>
      <div>
        <img class="persion-img" :src="imgUrl" />
        <div class="persion-title">{{ title }}</div>
        <div style="padding: 18px 58px 58px">
          <span v-html="text"></span>
        </div>
      </div>
      
    </el-dialog>
  </div>

  <!-- </div> -->
</template>
<script>
import vueSeamlessScroll from "vue-seamless-scroll";
export default {
  name: "swiper",
  components: {
    vueSeamlessScroll,
  },
  data() {
    return {
      imgData1: [],
      imgData2: [],
      classOption1: {
        autoPlay: true,
        limitMoveNum: 2,
        direction: 2, // 0向下 1向上 2向左 3向右
        hoverStop: false,
        step: 0.5,
      },
      classOption2: {
        autoPlay: true,
        limitMoveNum: 2,
        direction: 3, // 0向下 1向上 2向左 3向右
        hoverStop: false,
        step: 0.5,
      },
      key1: 1,
      key2: 2,

      dialogVisible: false,
      imgUrl: '',
      title: '',
      text: ''
    };
  },
  async mounted() {
    let res = await this.$api.get("/navsysBoot/navFront/staffList?type=2");
    if (res.success) {
      // this.imgData = res.result;
      const half = Math.ceil(res.result.length / 2);

      this.imgData1 = res.result.splice(0, half);
      this.imgData2 = res.result.splice(-half);
    }
  },
  methods: {
    async goDetail(item) {
      this.imgUrl = 'http://120.224.10.23:18060/navsysBoot/sys/common/static/' + item.attachment;
      const res = await this.$api.get('/navsysBoot/navFront/staffDetailById?id=' + item.id);
      if (res.success) {
        this.title = res.result.title;
        this.text = res.result.remarks;
      }
      // this.classOption.autoPlay = false;
      this.dialogVisible = true;
      console.log("11", item);
    },
    closeDialog() {
      this.dialogVisible = false;
      this.$nextTick(() => {
        // this.key++;
        // this.classOption.autoPlay = true;
      });
    },
  },
};
</script>
<style lang="scss" scoped>
// .container {
//   width: 100%;
//   height: 100%;
//   overflow: auto;
// }
.swiper-wrap {
  width: 3414px;
  height: 1920px;
  background: url("../assets/images/swiper/bg.png") no-repeat;
  background-size: 100%;
  padding-top: 330px;
  overflow: hidden;
}
.theme-content {
  width: 100%;
  height: 530px;
  transform: rotate(-4deg);

  .seamless-wrap {
    width: 100%;
    height: 370px;
    overflow: hidden;
    margin-bottom: 210px;
    .img-box {
      display: flex;
      .img-content {
        width: 500px;
        height: 370px;
        margin-right: 100px;
        // background: url("../assets/images/swiper/photo-border.png") no-repeat;
        // background-size: 100% 100%;
        padding: 8px;
        overflow: hidden;
        border: 4px solid rgb(210, 154, 64);
        cursor: pointer;
        img {
          width: 100%;
          height: 100%;
          border: 8px solid rgb(223, 2220, 162);
          border-bottom-color: rgb(223, 146, 47);
          border-left-color: rgb(223, 146, 47);
        }
      }
    }
  }
}
</style>
<style lang="scss">
.swiper-wrap {
  .custom-img-dialog {
    .el-dialog__header {
      display: none;
    }
    .el-dialog__body {
      padding: 0;
      .el-icon-circle-close {
        position: absolute;
        top: -78px;
        right: -78px;
        font-size: 100px;
        color: #fff;
        cursor: pointer;
      }
      .dialog-body-content-base-style {
        height: 100%;
      }
      .persion-img {
        width: 100%;
        height: 75%;
      }
      .persion-title {
        height: 50px;
        line-height: 50px;
        text-align: center;
        font-size: 20px;
        font-weight: 500;
      }
    }
  }
}
</style>
